import request, { Method } from "@/utils/request.js";


/**
 * 获取接收的领养申请信息列表
 */
export function getMyReceive(param) {
    return request({
      url: `/apply/my/receive`,
      method: Method.POST,
      needToken: true,
      data: param,  
      headers: {  'Content-Type': 'application/json'}
    });
}

/**
 * 获取接收的领养申请信息
 */
export function getReceiveDetail(id) {
    return request({
      url: `/apply/my/receive/${id}`,
      method: Method.GET,
      needToken: true,
      headers: {  'Content-Type': 'application/json'}
    });
}