<template>
    <div>
      <el-row style="padding: 0 20px">
          <el-select @change="getApplyCardsPage" style="width: 120px" v-model="wikiState" placeholder="稿件状态">
              <el-option
              v-for="item, index in wikiStateOptions"
              :key="index"
              :label="item.label"
              :value="item.value">
              </el-option>
          </el-select>
          <el-select @change="getApplyCardsPage" style="margin-left: 14px; width: 120px;" v-model="wikiType" placeholder="百科类型">
              <el-option
              v-for="item, index in wikiTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.name">
              </el-option>
          </el-select>
          <el-select style="margin-left: 14px; width: 120px" v-model="wikiOrder" placeholder="排序方式">
              <el-option
              v-for="item, index in wikiOrderOptions"
              :key="index"
              :label="item.label"
              :value="item.value">
              </el-option>
          </el-select>
          <el-switch style="margin-left: 14px;"
          v-model="wikiOrderType"
          @change="getApplyCardsPage"
          active-text="升序"
          active-value="asc"
          inactive-text="降序"
          inactive-value="desc">
          </el-switch>
          <el-input class="my-wiki-search"
              placeholder="搜索百科"
              clearable
              v-model="wikiSearchTag">
              <i @click="myReceiveSearch" slot="prefix" class="el-input__icon el-icon-search"></i>
          </el-input>
      </el-row>
      <el-main>
          <div class="card-container">

            <div class="card-body" v-for="(item) in cardsData" :key="item.wikiId">
                <div class="card-image" @click="toAnimlDetail(item.id)">
                    <a><img :src=item.adopt.cover class="image"></a>
                </div>
                <div class="card-info">
                    <div class="card-info-title">{{ item.adopt.title }}</div>
                    <div class="card-info-middle">
                        <div class="info-status">
                            <span >发布时间：{{ item.adopt.createTime }}</span>
                            <span style="margin-top: 4px;">申请时间：{{ item.createTime }}</span>

                        </div>
                    </div>
                     
                </div>
                <!-- 申请人信息 -->
                <div class="info-creator">
                    <div class="creator" style="display: flex;flex-flow: column;justify-content: center;align-items: center;">
                        <el-avatar :size="36" src="http://s1osmcysm.hn-bkt.clouddn.com/Avatar/15/12/f44b257f-c591-43f7-b170-fda675f2b6a3.jpg"></el-avatar>
                        <div style="margin-top: 6px;" class="creator-name"><span>{{ 'AzMiao' }}</span></div>
                    </div>
                    <div class="creator-info" style="margin-left: 24px;">
                        <div>
                            {{ item.name }}
                            {{ item.phone }}
                        </div>
                        <!-- <div style="margin-top: 3px;"><span>e-mail:</span>{{ ' 123123@foxmail.com' }}</div> -->
                        <div style="margin-top: 3px;">
                            <p style="margin: 0;-webkit-line-clamp: 2;">
                            {{ item.description }}
                            </p>
                        </div>
                       
                    </div>
                </div>

                <!-- 操作 -->   

                <div class="apply-options" style="display: flex; align-items: center;margin: 0 10% 0 5% ;">
                    <div style="display: flex;">
                        <el-button size="small" plain @click="showApplyDetail(item.id)">详细</el-button>
                        <!-- <el-button size="small" type="danger" plain>拒绝</el-button> -->
                        <el-button size="small" plain>更多</el-button>
                    </div>
                </div>
                
            </div>
            <!-- 详细信息展示 -->
            <el-dialog
            :visible.sync="isShowApplyDetail"
            width="32%"
            top="15vh"
            center>
                <el-descriptions title="申请信息"
                column="2"
                >
                    <el-descriptions-item label="用户名">{{ 'AzMiao' }}</el-descriptions-item>
                    <el-descriptions-item label="姓名">{{ '陈先生' }}</el-descriptions-item>
                    <el-descriptions-item label="手机号">{{ '16717326633' }}</el-descriptions-item>
                    <el-descriptions-item label="微信">{{ 'wechat' }}</el-descriptions-item>
                    <el-descriptions-item label="居住地">{{ 'region' }}</el-descriptions-item>

                    <el-descriptions-item label="QQ">{{ 'qq' }}</el-descriptions-item>

                    <el-descriptions-item label="环境描述">{{ 'The Ewokian is a designer dog breed developed by crossing two toy dogs, the Havanese and the Pomeranian. Their teddy-bear look charms everybody!   ' }}</el-descriptions-item>
                </el-descriptions>  

                <span slot="footer" class="dialog-footer">
                    <el-button type="success" plain>通 过</el-button>
                    <el-button type="danger" plain>拒 绝</el-button>
                    <el-button type="primary" plain @click="isShowApplyDetail = false">取 消</el-button>
                </span>
            </el-dialog>

          </div>
          <div class="block" v-if="total">
              <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="pageSize"
              :page-sizes="[12, 16, 20, 24]"
              layout="sizes, prev, pager, next, total, jumper"
              :total="total">
              </el-pagination>
          </div>
      </el-main>
  
    </div>
  </template>
  
