    created() {},
    mounted() {
      console.log(storage.getItem('receiveCardCurrentPage'));
      console.log(storage.getItem('receiveCardPageSize'));
      this.currentPage = storage.getItem('receiveCardCurrentPage')==undefined?1:parseInt(storage.getItem('receiveCardCurrentPage'));
      this.pageSize = storage.getItem('receiveCardPageSize')==undefined?12:parseInt(storage.getItem('receiveCardPageSize'));
      let params = {
          wikiType: this.wikiType,
          currentPage: this.currentPage,
          pageSize: this.pageSize,
      }
      this.getApplyCardsPage()
  
      // 获取类型选项
      apiWiki.getWikiTemplateOptions().then((res)=>{
          if(res.success){
              this.wikiTypeOptions = JSON.parse(res.result);
          }else{
              this.$message.error(res.message);
          }
      })
    },
    methods: {
      showApplyDetail(id) {
        apiApply.getReceiveDetail(id).then((res)=>{
            if (res.success) {
                this.applyDetail = res.result;
                this.$message.success('getReceiveDetail');
                this.isShowApplyDetail = true;
            } else {
            this.$message.error(res.message);
            }
        })
      },
      editWiki(item) {
          // 获取wikiID 路由跳转到编辑页面
          let wikiId = item.id;
          this.$router.push({path: '/petswiki/wikiEdit/' + wikiId});
      },
      delWiki(item) {
          // 弹出框
          this.$confirm('确定删除百科?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
          }).then(() => {
          let params = {
              wikiId: item.id,
          }
          apiWiki.deleteWiki(params).then((res)=>{
              if (res.success) {
              // this.wiki = res.result;
                  this.$message.success(res.result);
                  this.getApplyCardsPage();
              } else {
              this.$message.error(res.message);
              }
            })
          })
  
      },
      
      handleSizeChange(val) {
          storage.setItem('receiveCardCurrentPage', this.currentPage);
          storage.setItem('receiveCardPageSize', val);
          this.pageSize = val;
          this.getApplyCardsPage()
      },
      handleCurrentChange(val) {
          storage.setItem('receiveCardCurrentPage', val);
          storage.setItem('receiveCardPageSize', this.pageSize);
          this.currentPage = val;
          this.getApplyCardsPage()
      },
      getApplyCardsPage() {
          // 发送请求获取数据
          let params = {
            //   wikiState: this.wikiState,
            //   wikiType: this.wikiType,
              currentPage: this.currentPage,
            //   wikiOrder: this.wikiOrder,
            //   wikiOrderType: this.wikiOrderType,
              pageSize: this.pageSize,
          }
          apiApply.getMyReceive(params).then(res=>{
              if(res.success){
                  this.$message.success('ApplyCard获取成功');
                  this.cardsData = res.result.records;
                  this.total = res.result.total;
  
              }else{
                  this.$message.error(res.message);
              }
          })
      },
      myReceiveSearch() {
          // 发送请求获取数据
          let params = {
              wikiState: this.wikiState,
              wikiType: this.wikiType,
              currentPage: this.currentPage,
              wikiOrder: this.wikiOrder,
              wikiOrderType: this.wikiOrderType,
              pageSize: this.pageSize,
              searchTag: this.wikiSearchTag,
          }
          apiWiki.getMyWikiCards(params).then(res=>{
              if(res.success){
                  this.$message.success('百科Card获取成功');
                  this.cardsData = res.result.records;
                  this.total = res.result.total;
  
              }else{
                  this.$message.error(res.message);
              }
          })
      },
      toAdoptInfo(id) {
          // 路由跳转到动物详细页面
          this.$router.push({path: '/petswiki/wikiInfo/' + id});
      },
    },
    
  };
